.col-md-12 .thumb {
  display: block;
  margin: 0 auto;
  width: auto; }

.col-md-8 .thumb {
  width: 100%; }

.close-modal {
  right: -18px;
  position: absolute;
  top: -18px;
  background: none;
  border: 0;
  font-size: 21px;
  -webkit-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear; }
  .close-modal:hover {
    color: #3a98b7; }

#experience {
  margin: 0; }
  #experience h2 {
    text-align: right; }
  #experience .p-items {
    margin-top: 2rem; }
  #experience .btn-group-vertical > .btn,
  #experience .btn-group > .btn {
