    border-radius: 0; }

.ReactModal__Overlay {
  z-index: 9328748736;
  background-color: rgba(25, 25, 25, 0.75) !important; }

.Select.is-clearable.is-searchable {
  z-index: 99893764573; }

.grid-main-container {
  top: 23px; }



